import $                from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import model from 'UNIT_PATH/model';
import view from 'UNIT_PATH/view';
import util from 'UTILS_PATH/helper';

let timer = null;
let disTimeArr = VARIABLE.GLOBAL.RUSH_BUY.disTimeArr;

function main() {
	if(!$('.party-item').length) return;
	
	$('.party-item').each(function(i, v) {
		var data = {};

		if($(this).attr('data-txt') == '距结束'){
			data.type = '0';
		}else{
			data.type = '1';
		}

		data.disTime = parseInt($(this).attr('data-distime'), 10);
		disTimeArr.push(data);
	});

	timerRun();
	if(timer) {
		clearInterval(timer);
		timer = null;
	}
	timer = setInterval(timerRun, 1000);
}

function timerRun() {
	var partyTimer = $('#party-timer');
	var disTime = disTimeArr[VARIABLE.GLOBAL.RUSH_BUY.indicator].disTime;
	var time_data = util.timeTransform(disTime / 1000);

	// 时
	$('.hour', partyTimer).eq(0).text(time_data.hour);

	// 分
	$('.minute', partyTimer).eq(0).text(time_data.min);

	// 秒
	$('.second', partyTimer).eq(0).text(time_data.sec);

	for(var i = 0; i < disTimeArr.length; i++) {
		disTimeArr[i].disTime -= 1000;
	}

	if(disTime <= 0) {
		clearInterval(timer);
		timer = null;
		model.getNewTime(function(data) {
			window.location.href = window.location.href;
		});
	}
}

export default main;


