import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';

// 图片懒加载
var lazy = {
    init: function(opt) {
        var that = this;
        var op = {
            anim: true
        };
        $.extend(op, opt);
        this.op = op;
        return that.img.init(op);
    },

    img_each: function(img_lazy) {
        var _this = this;

        img_lazy.each(function(index, node) {
            if (!$(this).attr('data-layzr')) {
                return;
            }

            if (!_this.inViewport(this)) {
                return
            }

            _this.act(this);
        });
    },

    dataEach: function(dataItem) {
         var _this = this;

        dataItem.each(function(index, node) {
            if (!$(this).attr('data-xhr-layzr')) {
                return;
            }

            if (!_this.inViewport(this)) {
                return
            }

            _this.op.loadData && _this.op.loadData($(this));
        });
    },

    act: function(_self) {
        var $_self = $(_self);
        var _this = this;

        if ($_self.attr('loaded')) {
            return;
        }
        var img = new Image(), original = $_self.attr('data-layzr');

        if(_this.op.srcTransform && _this.op.srcTransform(original, _self)) {
            original = _this.op.srcTransform(original, _self);
        }

        img.onload = function() {
            $_self.attr('src', original).removeAttr('data-layzr');
            _this.op.load_sucess && _this.op.load_sucess(_self, true, img);
        }
        img.onerror = function() {
            _this.op.load_sucess && _this.op.load_sucess(_self, false, img);
        }

        original && (img.src = original);
        $_self.attr('loaded', true);
    },

    inViewport: function(el) {
        var top = window.pageYOffset - VARIABLE.CONFIG.PRE_LOAD;
        var btm = window.pageYOffset + window.innerHeight + VARIABLE.CONFIG.PRE_LOAD;
        var elTop = $(el).offset().top;
        var elBottom =  elTop + $(el).height();

        return elTop >= top && elBottom <= btm;
    },

    refreshImg: function() {
        this.img.imgs = $('img[data-layzr]');
        this.img_each(this.img.imgs);
        this.img.dataItem = $('*[data-xhr-layzr]');
        this.dataEach(this.img.dataItem);
    },

    img: {
        imgs: $('img[data-layzr]'),
        dataItem: $('*[data-xhr-layzr]'),
        init: function(n) {
            var _this = this;
            var lazyScrollTimer = null;

            $(window).bind('scroll', function() {
                clearTimeout(lazyScrollTimer);
                lazyScrollTimer = setTimeout(function() {
                    lazy.img_each(_this.imgs);
                    lazy.dataEach(_this.dataItem);
                }, 300);
            });

            lazy.img_each(_this.imgs);
            lazy.dataEach(_this.dataItem);
            return lazy;
        }
    }
}

// 基于anima-scroll懒加载
var animaScrollLazy = {
    init: function(opt) {
        var that = this;
        var op = {
            anim: true
        };
        $.extend(op, opt);
        this.op = op;
        return that.img.init(op);
    },

    img_each: function(img_lazy) {
        var _this = this;

        img_lazy.each(function(index, node) {
            if (!$(this).attr('data-layzr')) {
                return;
            }

            if (!_this.inViewport(this)) {
                return
            }

            _this.act(this);
        });
    },

    dataEach: function(dataItem) {
         var _this = this;

        dataItem.each(function(index, node) {
            if (!$(this).attr('data-xhr-layzr')) {
                return;
            }

            if (!_this.inViewport(this)) {
                return
            }
            _this.op.loadData && _this.op.loadData($(this));
        });
    },

    act: function(_self) {
        var $_self = $(_self);
        var _this = this;

        if ($_self.attr('loaded')) {
            return;
        }
        var img = new Image(), original = $_self.attr('data-layzr');

        img.onload = function() {
            $_self.attr('src', original).removeAttr('data-layzr');
            _this.op.load_sucess && _this.op.load_sucess(_self);
        }
        img.onerror = function() {
            _this.op.load_sucess && _this.op.load_sucess(_self);
        }

        original && (img.src = original);
        $_self.attr('loaded', true);
    },

    inViewport: function(el) {
        var _el = $(el);
        var top = this.img.scroll_view.offset().top - VARIABLE.CONFIG.PRE_LOAD;
        var btm = this.img.scroll_view.offset().top + this.op.iscroller.getViewHeight() + VARIABLE.CONFIG.PRE_LOAD;
        var el_top = _el.offset().top;
        var el_bottom = el_top + _el.height();

        return el_bottom <= btm && el_top >= top;
    },

    refreshImg: function() {
        this.img.imgs = $('img[data-layzr]');
        this.img_each(this.img.imgs);
        this.img.dataItem = $('*[data-xhr-layzr]');
        this.dataEach(this.img.dataItem);
    },

    img: {
        imgs: $('img[data-layzr]'),
        dataItem: $('*[data-xhr-layzr]'),
        scroll_view: $('#scroll-view'),
        init: function(n) {
            var _this = this;

            n.iscroller.addScrollendHandler(function() {
                animaScrollLazy.img_each(_this.imgs);
                animaScrollLazy.dataEach(_this.dataItem);
            });
            animaScrollLazy.img_each(_this.imgs);
            animaScrollLazy.dataEach(_this.dataItem);
            return animaScrollLazy;
        }
    }
}

module.exports = {
    lazy: lazy,
    animaScrollLazy: animaScrollLazy
};



