import $ from 'ZEPTO_PATH';
import Swiper from 'SWIPER_JS_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import updateSlider from 'UNIT_PATH/updateSlider'

function init() {
	var slider_config = {};
	var sliderSwiper;

	if($('.red-slider-wraper li').length > 1) {
		slider_config = {
            loop: true,
            autoplay: 5000,
            autoplayDisableOnInteraction: false,
            pagination : '.red-slider-pagination',
            onImagesReady: function() {
            },
            onInit: function() {
            	
            }
        };
	}

	if($('.red-slider-wraper').length) {
		sliderSwiper = new Swiper('.red-slider-wraper', slider_config);
	}

	// 浏览器端旋转设备时刷新尺寸
	if(VARIABLE.CONFIG.VS != 'jdapp') {
		$(window).bind('orientationchange', function() {
			updateSlider(sliderSwiper);
		});
	}
}

module.exports = {
	init: init
}


