import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import updateSlider from 'UNIT_PATH/updateSlider'

function init(container) {
	var actSlider = Swiper(container, {
		loop: false,
		slidesPerView : 'auto',
		freeMode: true,
		preventClicks : true,
		freeModeSticky: true,
		freeModeMomentumRatio: 0.2,
		onInit: function() {
		
		},
		onClick: function(swiper) {
			window.location.href = $(swiper.clickedSlide).find('a').attr('_href')
		}
	});

	$(container).find('a').each(function(i, v) {
		var href = $(this).attr('href');

		$(this).attr('_href', href).attr('href', 'javascript:;');
	});

	// 浏览器端旋转设备时刷新尺寸
	if(VARIABLE.CONFIG.VS != 'jdapp') {
		$(window).bind('orientationchange', function() {
			var goods_slider_li = $('#goods-rec-wrapper li');
			var client_width = document.documentElement.clientWidth;

			goods_slider_li.css('width', Math.ceil(client_width / 3.5));
			updateSlider(actSlider);
		});
	};

	return actSlider;
}

module.exports = init;


