import $ from 'ZEPTO_PATH';

function start() {
	var pageType = $('body').attr('data-type');

	if(!$('#footer-nav').length) return;

	$('#footer-nav a').each(function(i, v) {
		var _this = $(this);
		var thisType = _this.attr('type');

		// if(thisType == pageType) {
		// 	_this.closest('li').addClass('active').find('img').attr('src',  _this.attr('img_active'));
		// }else{
		// 	_this.closest('li').addClass('active').find('img').attr('src',  _this.attr('img_active'));
		// }

		if(thisType == pageType) {
			_this.closest('li').addClass('active');
			_this.append('<img src="'+ _this.attr('img_active') +'">');
		}else{
			_this.append('<img src="'+ _this.attr('img') +'">');
		}
	});
}

module.exports = start;


