import $                from 'ZEPTO_PATH';
import VARIABLE         from 'UNIT_PATH/variable';
import header           from 'MODULES_PATH/header.js';
import layout           from 'MODULES_PATH/layout';
import redSlider        from 'MODULES_PATH/redSlider';
import FooterAd         from 'MODULES_PATH/footerAd';
import goTop            from 'MODULES_PATH/goTop';
import LazyLoad         from 'MODULES_PATH/indexLazyLoad';
import footBar          from 'MODULES_PATH/footBar';
import rushBuyTimer     from 'MODULES_PATH/rushBuyTimer';
import rushBuyListSwitch from 'MODULES_PATH/rushBuyListSwitch';
import rushBuyPage      from 'MODULES_PATH/rushBuyPage';
import model            from 'UNIT_PATH/model';
import view             from 'UNIT_PATH/view';

window.$ = $;

// 页面布局调整
layout();

// 回到顶部按钮
goTop();

// 懒加载
LazyLoad();

// 公共头部
header.headerInit();

// 轮播图
redSlider.init();

// 开启倒计时
rushBuyTimer();

// tab切换
rushBuyListSwitch();

var goods_item = $('.item-group').last();
var skuidsv = goods_item.attr('data-skus');

model.getPriceData(skuidsv, function(data) {
	if($('.party-item').eq(0).attr('data-txt') == '距结束') {
		view.renderRushbuyPrice(data, skuidsv);
	}else{
		view.renderRushBuyPPrice(data, skuidsv);
	}
});

// 库存获取
model.getStockData(skuidsv, function(data) {
	view.renderRushBuyStock(data);
});

// 列表翻页
rushBuyPage();

// 尾部广告
var footerAd = new FooterAd();

footerAd.init();

// 底部导航
footBar();


