import $ from 'ZEPTO_PATH';
import actGoodsSlider from 'MODULES_PATH/actGoodsSlider';
import VARIABLE from 'UNIT_PATH/variable';
import updateSlider from 'UNIT_PATH/updateSlider';

var model = require('UNIT_PATH/model');
var view = require('UNIT_PATH/view');

function main(item) {
	var priceAndStockRequest = 0;
	var skuidsv = item.attr('data-skus');
	var loadState = item.attr('data-xhr-layzr');
	var slider;

	if(loadState == 'loading' || loadState == 'loaded') return;

	item.attr('data-xhr-layzr', 'loading');
	slider = actGoodsSlider(item.find('.swiper-container'));
	
	// 价格获取
	model.getPriceData(skuidsv, function(data) {
    	view.renderActPrice(data, skuidsv, item);
    	priceAndStockRequest++;
    	if(priceAndStockRequest == 2) {
    		clearProduct(item, slider);
    	}
    });

	// 库存获取
    model.getStockData(skuidsv, function(data) {
		view.renderStock(data, item);
		priceAndStockRequest++;
		if(priceAndStockRequest == 2) {
			clearProduct(item, slider);
    	}
	});
}

// 清除多余的商品
function clearProduct(item, slider) {
	var thisLi = item.find('li[data-goods-id]');

	if(thisLi.length > 8) {
		for(var i = thisLi.length; i > 8; i--) {
			thisLi.eq(i - 1).remove();
		}
	}

	if($(this).find('li[data-goods-id]').length <= VARIABLE.CONFIG.ACTGOODS_MIN) {
		$(this).prev('.item').addClass('mb10');
		$(this).remove();
	}

	updateSlider(slider);
	// actGoodsSlider(item.find('.swiper-container'));
	VARIABLE.GLOBAL.SCROLL_OBJ && VARIABLE.GLOBAL.SCROLL_OBJ.refresh();
	item.attr('data-xhr-layzr', 'loaded');
}

module.exports = main;


