import $ from 'ZEPTO_PATH';
var VARIABLE = require('UNIT_PATH/variable'); 
var itemPos = [];

function start() {
	var items      = $('.item');
	var go_top_btn = $('#go-top');
	var count_text = $('#count');
	var count_now  = $('#now');
	var back_text  = $('#back');
	var total_text = $('#total');
	var SCROLL     = VARIABLE.GLOBAL.SCROLL_OBJ;
	var item_count_inview, wrapper_height, item_height, first_item_top, scroll_view_top;

	// 基于系统自带滚动
	if(!VARIABLE.CONFIG.ANIMA_SCROLL) {
		var scrollTop, wHeight;

		$('#go-top').click(function() {
			$(window).scrollTop(0);
		});

		$(window).bind('scroll', function() {
			scrollTop = $(window).scrollTop();
			wHeight = $(window).height();

			if(scrollTop >= wHeight){
				go_top_btn.removeClass('none');
			}else{
				go_top_btn.addClass('none');
			}
		});
		return;
	}

	if(!SCROLL) return; 

	itemPosCom();

	// 填充活动总数
	total_text.text(items.length);

	// 回到顶部
	go_top_btn.click(function() {
		if(go_top_btn.find('.back').hasClass('none')) {
			return;
		}
		SCROLL.scrollTo(0, true);
		go_top_btn.addClass('none');
	});

	// 滚动之前(手指已经与屏幕接触)
	SCROLL.addScrollstartHandler(function() {
		items = $('.item');
		wrapper_height = SCROLL.getViewHeight();
		itemPosCom();
	});

	var num_last = 0;
	var targetIndex = 0;

	// 滚动时
	SCROLL.addScrollingHandler(function() {
		var y = SCROLL.getScrollTop();

		if(itemPos[targetIndex] && y >= itemPos[targetIndex].top - wrapper_height) {
			targetIndex++;
			if(targetIndex > itemPos.length) {
				targetIndex = itemPos.length;
			}
		}

		var preIndex = targetIndex - 1;

		if(itemPos[preIndex] && y <= itemPos[preIndex].top - wrapper_height) {
			targetIndex--;
		}

		var itemCount = targetIndex;

		if(itemCount <= 0) {
			itemCount = 1;
		}



		if(itemCount != num_last) {
			count_now.text(itemCount);
			num_last = itemCount;
		}

		if(itemCount >= 8) {
			go_top_btn.removeClass('none');
		}else{
			go_top_btn.addClass('none');
		}

		// 显示计数器
		count_text.removeClass('none');
		back_text.addClass('none');
	});

	// 滚动停止时
	SCROLL.addScrollendHandler(function() {
		var itemCount = targetIndex;

		if(itemCount <= 0) {
			itemCount = 1;
		}

		if(itemCount >= 8) {
			go_top_btn.removeClass('none');
		}else{
			go_top_btn.addClass('none');
		}

		// 隐藏计数器
		count_text.addClass('none');
		back_text.removeClass('none');
	});
}

function itemPosCom() {
	var scroller  = $('#scroller');
	var items     = $('.item');

	itemPos.length = 0;
	items.each(function(i, v) {
		var y = $(this).offset().top - scroller.offset().top + $(this).height();

		itemPos.push({
			top: y,
			index: i + 1
		});
	});
}

module.exports = start;


