import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import model from 'UNIT_PATH/model';
import view from 'UNIT_PATH/view';
import util from 'UTILS_PATH/helper';
import Loading from 'UNIT_PATH/loading';

let disTimeArr = VARIABLE.GLOBAL.RUSH_BUY.disTimeArr;
let body_dom = $('body');

let main = () => {
	// 如果当前只有一场
	if(disTimeArr.length == 1) {
		// 只有正在进行的场次
		if(disTimeArr[0].type == '0') {
			$('.party-remind').text('抢购中 先下单先得哦');
			$('#timer-out .timer-txt').text('距结束');
		}else{
			// 只有预告场次
			$('.party-remind').text('即将开始 先下单先得哦');
			$('#timer-out .timer-txt').text('距开始');
		}
	}

	$('.party-item').click(function() {
		var _this = $(this);
		
		VARIABLE.GLOBAL.RUSH_BUY.indicator = $(this).index();
		$('.party-item').removeClass('active');
		$(this).addClass('active');
		$('#timer-out .timer-txt').text($(this).attr('data-txt'));

		if(_this.attr('data-txt') == '距结束') {
			$('.party-remind').text('抢购中 先下单先得哦');
		}else{
			$('.party-remind').text('即将开始 先下单先得哦');
		}

		disTimeSwitch();
		Loading.showLoading({
	        type: 'load-fullscreen',
	        loadingTxt: '数据获取中..'
	    }, function(_loadingHtml) {
	        $('.red-page').append(_loadingHtml);
	    });
		getCurPartyList(_this);
	});
}

let disTimeSwitch = () => {
	let partyTimer = $('#party-timer');
	let disTime = disTimeArr[VARIABLE.GLOBAL.RUSH_BUY.indicator].disTime;
	let time_data = util.timeTransform(disTime / 1000);

	// 时
	$('.hour', partyTimer).eq(0).text(time_data.hour);

	// 分
	$('.minute', partyTimer).eq(0).text(time_data.min);

	// 秒
	$('.second', partyTimer).eq(0).text(time_data.sec);
}

let getCurPartyList = (item) => {
	model.getRushBuyUsePage(1, function(data) {
		$('#rush-buy-list').html('');
		$('<div class="page-wraper"></div>').html(data).appendTo('#rush-buy-list');
		body_dom.attr('data-page', 1);

		if($('#rush-buy-list').html() == '') {
			var emptyDom = '<div class="empty">' +
                                '<p>宝贝被抢光啦</p>' +
                                '<p>下次早点来哟</p>' +
                            '</div>';

			$('#rush-buy-list').html(emptyDom);
			('body').attr('data-total-page', '1');
			return;
		}
		VARIABLE.GLOBAL.LAZY.refreshImg();
		$('body').attr('data-total-page', $('#rush-buy-list .item-group').attr('data-total-page'));

		var goods_item = $('.item-group').last();
		var skuidsv = goods_item.attr('data-skus');

		model.getPriceData(skuidsv, function(data) {
			if(item.attr('data-txt') == '距结束') {
				view.renderRushbuyPrice(data, skuidsv);
			}else{
				view.renderRushBuyPPrice(data, skuidsv);
			}
		});

		// 库存获取
		model.getStockData(skuidsv, function(data) {
			view.renderRushBuyStock(data);
		});
		Loading.hideLoading('load-fullscreen');
	});
}

export default main;


