import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import LAZY_LOAD from 'UNIT_PATH/lazy_load';
import goods     from 'MODULES_PATH/indexGoods';

function lazyLoad() {
	// 如果不是ios，就是用系统滚动懒加载
	if(!VARIABLE.CONFIG.ANIMA_SCROLL) {
		VARIABLE.GLOBAL.LAZY = LAZY_LOAD.lazy.init({
			load_sucess: function(img) {
				var item        = $(img).closest('.item');
				var logo_img    = item.find('img[data-logo-layzr]');
				var logo_url    = logo_img.attr('data-logo-layzr');

				$(img).addClass('fadeIn');

				// 加载logo图片
				if(logo_url) {
					var Img = new Image();

					Img.onload = function() {
						logo_img.attr('src', logo_url);
					}

					Img.src = logo_url;
				}
			},
			loadData: function(item) {
				// 活动外露商品
				//goods(item);
			}
		});
		return;
	};

	if(!VARIABLE.GLOBAL.SCROLL_OBJ) return;
	VARIABLE.GLOBAL.LAZY = LAZY_LOAD.animaScrollLazy.init({
		iscroller: VARIABLE.GLOBAL.SCROLL_OBJ,
		load_sucess: function(img) {
			var item        = $(img).closest('.item');
			var logo_img    = item.find('img[data-logo-layzr]');
			var logo_url    = logo_img.attr('data-logo-layzr');
	
			// 加载logo图片
			if(logo_url) {
				var Img = new Image();
	
				Img.onload = function() {
					logo_img.attr('src', logo_url);
				}
	
				Img.src = logo_url;
			}
		},
		loadData: function(item) {
			// 活动外露商品
			//goods(item);
		}
	});
}

module.exports = lazyLoad;


