import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import model from 'UNIT_PATH/model';
import view from 'UNIT_PATH/view';
import util from 'UTILS_PATH/helper';
import Loading from 'UNIT_PATH/loading';

let get_more_loading   = false;
let lazy = VARIABLE.GLOBAL.LAZY;

let get_page = () => {
	var scrollTop = $(window).scrollTop();
    var dHeight   = $(document).height();
    var wHeight   = $(window).height();
    var body_dom  = $('body');

    if(scrollTop >= dHeight - wHeight - 500){
    	if(get_more_loading) {
			return;
		}
		get_more_loading = true;

		var page_now   = parseInt(body_dom.attr('data-page'), 10) + 1;
		var page_total = parseInt(body_dom.attr('data-total-page'), 10);

		// 判断是不是已经到了最后一页
		if(page_now > page_total){
            get_more_loading = false;
            return;
        }

		// 翻页请求
        model.getRushBuyMorePage(function(data) {
			$('<div class="page-wraper"></div>').html(data).appendTo('#rush-buy-list');
			lazy.refreshImg();
			var goods_item = $('.item-group').last();
			var skuidsv = goods_item.attr('data-skus');

			model.getPriceData(skuidsv, function(data) {
				if($('.party-item.active').find('.party-txt').text() == '抢购中') {
					view.renderRushbuyPrice(data, skuidsv);
				}else{
					view.renderRushBuyPPrice(data, skuidsv);
				}
			});
			// 库存获取
			model.getStockData(skuidsv, function(data) {
				view.renderRushBuyStock(data);
			});
			get_more_loading = false;
		});
    }
}

let main = () => {
	var rush_buy_list = $('#rush-buy-list');

	get_page();
	$(window).bind('scroll', get_page);
}



export default main;


